import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Params } from '@angular/router';
import { DomSanitizer, SafeResourceUrl } from '@angular/platform-browser';

import { ProviderService } from './../../services/provider.service';
import { WatcherService } from './../../services/watcher.service';
import { ValidateService } from './../../services/validate.service';
import { FilmService } from './../../services/film.service';

@Component({
  selector: 'app-film-detail',
  templateUrl: './film-detail.component.html',
  styleUrls: ['./film-detail.component.css']
})
export class FilmDetailComponent implements OnInit {
  
  paramsId: string;
  film: any;
  watch: boolean = false;
  video: SafeResourceUrl;
  watchlaterAction = { id: '', cek: '' };
  loveAction = { id: '', cek: '' };
  review: string;
  
  constructor( 
    private watcherService: WatcherService,
    private filmService: FilmService,
    private validate: ValidateService,
    private provider: ProviderService,
    private route: ActivatedRoute,
    private sanitizer: DomSanitizer
  ) { }

  ngOnInit() {
    this.getFilmId();
    this.getFilmDetail();
    this.getWatchlater();
    this.getLove();
  }

  getFilmDetail() {
    this.provider.isLoading = true;

    this.filmService.filmDetail(this.paramsId).subscribe(data => {
      this.film = data.data;
      this.provider.isLoading = false;
    });
  }

  getFilmId() {
    this.route.params.subscribe(params => {
        this.paramsId = params['id'];
     });
  }

  onTonton() {
    this.watcherService.tonton(this.film._id).subscribe(data => {
      if(data.success) {
        this.provider.toast(data.message, 2000, 'Green');
        this.video = this.sanitizer.bypassSecurityTrustResourceUrl('film/video/'+this.film.video);
        this.watch = true;
      }
      else
        this.provider.toast(data.message, 2000, 'Green');
    });
  }

  onKeluar() {
    this.video = null;
    this.watch = false;
  }

  getWatchlater() {
    this.watcherService.watchlaterCek(this.paramsId).subscribe(data => {
      this.watchlaterAction.id = data.data.id;
      this.watchlaterAction.cek = data.data.cek;
    });
  }

  onWatchlater() {
    const action = {
      actionId: this.watchlaterAction.id,
      filmId: this.film._id
    }

    this.watcherService.watchlater(action).subscribe(data => {
      this.getWatchlater();
    });
  }

  getLove() {
    this.watcherService.loveCek(this.paramsId).subscribe(data => {
      this.loveAction.id = data.data.id;
      this.loveAction.cek = data.data.cek;
    });
  }

  onLove() {
    const action = {
      actionId: this.loveAction.id,
      filmId: this.film._id
    }

    this.watcherService.love(action).subscribe(data => {
      this.getLove();
    });
  }

  onReview() {
    const review = {
      text: this.review,
      filmId: this.film._id
    }

    if(this.validate.review(review)) {
      this.watcherService.review(review).subscribe(data => {
        this.getFilmDetail();
      });
    }
  }

}


