import { Component, OnInit, OnDestroy } from '@angular/core';
import { DomSanitizer, SafeUrl } from '@angular/platform-browser';
import { FileUploader } from 'ng2-file-upload';

import { UserService } from './../../../services/user.service';
import { FilmmakerService } from './../../../services/filmmaker.service';
import { ValidateService } from './../../../services/validate.service';
import { ProviderService } from './../../../services/provider.service';

@Component({
  selector: 'app-filmmaker',
  templateUrl: './filmmaker.component.html',
  styleUrls: ['./filmmaker.component.css']
})
export class FilmmakerComponent implements OnInit, OnDestroy {

  user: any;
  filmSelected: any;

  uploaderPhoto: FileUploader = new FileUploader({url:'https://mesvie.herokuapp.com/mesvie/photo-upload', authToken: this.userService.loadToken()});
  previewPhoto: SafeUrl;

  loadmoreFilmId: any;
  loadmoreFilm: any;
  filmPerPage: number = 3;

  constructor( 
    private userService: UserService,
    private filmmakerService: FilmmakerService,
    private validate: ValidateService,
    private provider: ProviderService,
    private sanitizer: DomSanitizer
  ) { }

  ngOnInit() {
  	this.getUser();
    this.getPreview();
  }

  getUser() {
    this.provider.isLoading = true;
    
    this.userService.getUser().subscribe(data => {
      this.user = data.data;
      this.initLoadmore();
      this.provider.isLoading = false;
    });
  }

  getPreview() {
    this.uploaderPhoto.onAfterAddingFile = (fileItem) => {
      if(this.uploaderPhoto.queue.length > 1)
        this.uploaderPhoto.queue[0].remove();
      this.previewPhoto  = this.sanitizer.bypassSecurityTrustUrl(window.URL.createObjectURL(fileItem._file));
    }
  }

  onUpdateProfil(){
    if(this.validate.updateProfil(this.user)) {
      const updateUser = {
        fullname: this.user.fullname,
        phone: this.user.phone,
        address: this.user.address,
        bio: this.user.bio,
        photo: this.user.photo
      }

      if(this.previewPhoto) {
        this.uploaderPhoto.uploadAll();
        this.uploaderPhoto.onCompleteItem = (item: any, response: any, status: any, headers: any) => {
          var data = JSON.parse(response);
          updateUser.photo = data.filename;
          this.prosesUpdateProfil(updateUser);          
        };
      }
      else
        this.prosesUpdateProfil(updateUser);
    }
  }

  prosesUpdateProfil(updateUser) {
    this.userService.updateProfil(updateUser).subscribe(data => {
      if(data.success){
        this.provider.toast(data.message, 2000, 'Green');
        this.onCancelProfil();
      }
      else
        this.provider.toast(data.message, 2000, 'Red');
    });
  }

  onCancelProfil() {
    this.getUser();
    this.previewPhoto = null;
    this.provider.closeModalProfil();
  }

  getUpdateFilm(film) {
    this.filmSelected = film;
    this.provider.openModalFilm();
  }

  onUpdateFilm() {
    if(this.validate.filmUpdate(this.filmSelected)) {
      this.filmmakerService.filmUpdate(this.filmSelected).subscribe(data => {
        if(data.success){
          this.provider.toast(data.message, 2000, 'Green');
          this.onCancelFilm();
        }
        else
          this.provider.toast(data.message, 2000, 'Red');
      });
    }
  }

  onCancelFilm() {
    this.getUser();
    this.provider.closeModalFilm();
  }

  initLoadmore() {
    if(this.user.filmmaker._daftarfilm.length <= this.filmPerPage)
      this.loadmoreFilm = null;
    else
      this.loadmoreFilm = { hide: true, disable: false };
  }

  onLoadmoreFilm() {
    this.loadmoreFilm.hide = false;
    this.loadmoreFilm.disable = true;

    this.loadmoreFilmId = setInterval(() => {
      this.filmPerPage += 4;
      
      if(this.user.filmmaker._daftarfilm.length <= this.filmPerPage)
        this.loadmoreFilm = null;
      else {
        this.loadmoreFilm.hide = true;
        this.loadmoreFilm.disable = false;
      }
      clearInterval(this.loadmoreFilmId);
    }, 2000);
  }

  ngOnDestroy() {
    if(this.loadmoreFilmId)
      clearInterval(this.loadmoreFilmId);
  }

}


