import { Component, OnInit, OnDestroy } from '@angular/core';
import { DomSanitizer, SafeUrl } from '@angular/platform-browser';
import { FileUploader } from 'ng2-file-upload';

import { UserService } from './../../services/user.service';
import { WatcherService } from './../../services/watcher.service';
import { ValidateService } from './../../services/validate.service';
import { ProviderService } from './../../services/provider.service';

import { AppComponent } from './../../app.component';

@Component({
  selector: 'app-profil',
  templateUrl: './profil.component.html',
  styleUrls: ['./profil.component.css']
})
export class ProfilComponent implements OnInit, OnDestroy {

  user: any;

  uploaderPhoto: FileUploader = new FileUploader({url:'https://mesvie.herokuapp.com/mesvie/photo-upload', authToken: this.userService.loadToken()});
  previewPhoto: SafeUrl;

  loadmoreHistoryId: any;
  loadmoreHistory: any;;
  historyPerPage: number = 4;

  loadmoreWatchlaterId: any;
  loadmoreWatchlater: any;;
  watchlaterPerPage: number = 4;

  loadmoreLoveId: any;
  loadmoreLove: any;;
  lovePerPage: number = 4;

  constructor( 
    private userService: UserService,
    private watcherService: WatcherService,
    private validate: ValidateService, 
    private provider: ProviderService,
    private appComponent: AppComponent,
    private sanitizer: DomSanitizer
  ) { }

  ngOnInit() {
  	this.getUser();
    this.getPreview();
  }

  getUser() {
    this.provider.isLoading = true;

    this.userService.getUser().subscribe(data => {
      this.user = data.data;
      this.initLoadmore();
      this.provider.isLoading = false;
    });
  }

  getPreview() {
    this.uploaderPhoto.onAfterAddingFile = (fileItem) => {
      if(this.uploaderPhoto.queue.length > 1)
        this.uploaderPhoto.queue[0].remove();
      this.previewPhoto  = this.sanitizer.bypassSecurityTrustUrl(window.URL.createObjectURL(fileItem._file));
    }
  }

  onUpdate(){
    if(this.validate.updateProfil(this.user)) {
      const updateUser = {
        fullname: this.user.fullname,
        phone: this.user.phone,
        address: this.user.address,
        bio: this.user.bio,
        photo: this.user.photo
      }

      if(this.previewPhoto) {
        this.uploaderPhoto.uploadAll();
        this.uploaderPhoto.onCompleteItem = (item: any, response: any, status: any, headers: any) => {
          var data = JSON.parse(response);
          updateUser.photo = data.filename;
          this.prosesUpdate(updateUser);          
        };
      }
      else
        this.prosesUpdate(updateUser);
    }
  }

  prosesUpdate(updateUser) {
    this.userService.updateProfil(updateUser).subscribe(data => {
      if(data.success){
        this.provider.toast(data.message, 2000, 'Green');
        this.onCancel();
      }
      else
        this.provider.toast(data.message, 2000, 'Red');
    });
  }

  onCancel() {
    this.getUser();
    this.previewPhoto = null;
    this.provider.closeModalProfil();
  }

  showSearch() {
    this.appComponent.cssClass = true;
  }

  onWatchlater(filmId) {
    this.watcherService.watchlaterCek(filmId).subscribe(data => {
      const action = {
        actionId: data.data.id,
        filmId: filmId
      }

      this.watcherService.watchlater(action).subscribe(data => {
        this.provider.navRoute('/film/'+filmId);
      });  
    });
  }

  initLoadmore() {
    if(this.user.watcher._history.length <= this.historyPerPage)
      this.loadmoreHistory = null;
    else
      this.loadmoreHistory = { hide: true, disable: false };

    if(this.user.watcher._watchlater.length <= this.watchlaterPerPage)
      this.loadmoreWatchlater = null;
    else
      this.loadmoreWatchlater = { hide: true, disable: false };

    if(this.user.watcher._love.length <= this.lovePerPage)
      this.loadmoreLove = null;
    else
      this.loadmoreLove = { hide: true, disable: false };
  }

  onLoadmoreHistory() {
    this.loadmoreHistory.hide = false;
    this.loadmoreHistory.disable = true;

    this.loadmoreHistoryId = setInterval(() => {
      this.historyPerPage += 4;
      
      if(this.user.watcher._history.length <= this.historyPerPage)
        this.loadmoreHistory = null;
      else {
        this.loadmoreHistory.hide = true;
        this.loadmoreHistory.disable = false;
      }
      clearInterval(this.loadmoreHistoryId);
    }, 2000);
  }

  onLoadmoreWatchlater() {
    this.loadmoreWatchlater.hide = false;
    this.loadmoreWatchlater.disable = true;

    this.loadmoreWatchlaterId = setInterval(() => {
      this.watchlaterPerPage += 4;
      
      if(this.user.watcher._watchlater.length <= this.watchlaterPerPage)
        this.loadmoreWatchlater = null;
      else {
        this.loadmoreWatchlater.hide = true;
        this.loadmoreWatchlater.disable = false;
      }
      clearInterval(this.loadmoreWatchlaterId);
    }, 2000);
  }

  onLoadmoreLove() {
    this.loadmoreLove.hide = false;
    this.loadmoreLove.disable = true;

    this.loadmoreLoveId = setInterval(() => {
      this.lovePerPage += 4;
      
      if(this.user.watcher._love.length <= this.lovePerPage)
        this.loadmoreLove = null;
      else {
        this.loadmoreLove.hide = true;
        this.loadmoreLove.disable = false;
      }
      clearInterval(this.loadmoreLoveId);
    }, 2000);
  }

  ngOnDestroy() {
    if(this.loadmoreHistoryId)
      clearInterval(this.loadmoreHistoryId);
    if(this.loadmoreWatchlaterId)
      clearInterval(this.loadmoreWatchlaterId);
    if(this.loadmoreLoveId)
      clearInterval(this.loadmoreLoveId);
  }

}



