import { Component, OnInit } from '@angular/core';

import { TransaksiService } from './../../services/transaksi.service';
import { ProviderService } from './../../services/provider.service';

declare var snap: any;

@Component({
  selector: 'app-paket',
  templateUrl: './paket.component.html',
  styleUrls: ['./paket.component.css']
})
export class PaketComponent implements OnInit {

  constructor(
  	private transaksiService: TransaksiService,
  	private provider: ProviderService
  ) { }

  ngOnInit() {
  }

  onPurchase(type, quantity, price) {
    const payment = {
      type: type,
      quantity: quantity,
      price: price
    }

  	this.transaksiService.purchase(payment).subscribe(data => {
      snap.pay(data.token, {
        onSuccess: (result) => {
          let transaksi = {
            order_id: result.order_id,
            type: payment.type,
            quantity: payment.quantity,
            price: payment.price,
            paymentType: result.payment_type,
            status: result.transaction_status
          }
          this.transaksiService.transaksiAdd(transaksi).subscribe(ozil => {
            console.log(ozil)
          });
        },
        onPending: (result) => {
          let transaksi = {
            order_id: result.order_id,
            type: payment.type,
            quantity: payment.quantity,
            price: payment.price,
            paymentType: result.payment_type,
            status: result.transaction_status
          }
          this.transaksiService.transaksiAdd(transaksi).subscribe(ozil => {
            console.log(ozil)
          });
        }
      });
    });
  }

}



