import { Component, OnInit } from '@angular/core';

import { AuthService } from '../../services/auth.service';
import { UserService } from './../../services/user.service';
import { FilmService } from './../../services/film.service';
import { ProviderService } from './../../services/provider.service';

import { AppComponent } from './../../app.component';

@Component({
  selector: 'app-navbar',
  templateUrl: './navbar.component.html',
  styleUrls: ['./navbar.component.css']
})

export class NavbarComponent implements OnInit {

  user: any;
  films: any;

  search: string;
  isSearch: boolean = false;

  constructor( 
    private authService: AuthService,
    private userService: UserService,
    private filmService: FilmService,
    private provider: ProviderService,
    private appComponent: AppComponent
  ) { }

  ngOnInit() {
    if(this.provider.isLogin()) {
      this.getUser();
      this.getFilmAll();
    }
  }

  getUser() {
    this.userService.getUser().subscribe(data => {
      this.user = data.data;
    });
  }

  getFilmAll() {
    this.filmService.filmAll().subscribe(data => {
      this.films = data.data;
    });
  }

  showSearch() {
    this.appComponent.cssClass = true;
  }
  closeSearch() {
    this.appComponent.cssClass = false;
  }
  getSearch() {
    if(this.search)
      this.isSearch = true;
    else
      this.isSearch = false;
  }
  onFilmDetail(filmId) {
    this.provider.navRoute('/film/'+filmId);
    this.closeSearch();
  }

  onLogout(){
    this.authService.logout();
    this.provider.toast("You're logout now!", 2000, 'Green', '/welcome');
  }

}


