import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
	name: 'filter'
})

export class FilterPipe implements PipeTransform {
	transform(items: any[], property: string, value: string) {
        if (!value) return items;
        return (items || []).filter((item) => property.split(',').some(key => item.hasOwnProperty(key) && new RegExp(value, 'gi').test(item[key])));
    }
}


