import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { RouterModule, Routes } from '@angular/router';
import { MaterializeModule } from 'angular2-materialize';
import { FileUploadModule } from 'ng2-file-upload';
import { NgxPaginationModule } from 'ngx-pagination';

/* Guards */
import { NotAuthGuard } from './guards/not-auth.guard';
import { AuthGuard } from './guards/auth.guard';
import { WatcherGuard } from './guards/watcher.guard';
import { FilmmakerGuard } from './guards/filmmaker.guard';

/* Services */
import { AuthService } from './services/auth.service';
import { UserService } from './services/user.service';
import { WatcherService } from './services/watcher.service';
import { FilmmakerService } from './services/filmmaker.service';
import { FilmService } from './services/film.service';
import { TransaksiService } from './services/transaksi.service';
import { ValidateService } from './services/validate.service';
import { ProviderService } from './services/provider.service';

/* Pipes */
import { FilterPipe } from './pipes/filter.pipe';
import { OrderPipe } from './pipes/order.pipe';
import { TruncatePipe } from './pipes/truncate.pipe';

/* Pages */
import { AppComponent } from './app.component';
import { NavbarComponent } from './components/navbar/navbar.component';
import { HomeComponent } from './components/home/home.component';
import { RegisterComponent } from './components/register/register.component';
import { ActivateComponent } from './components/activate/activate.component';
import { LoginComponent } from './components/login/login.component';
import { DashboardComponent } from './components/dashboard/dashboard.component';
import { DaftarTransaksiComponent } from './components/daftar-transaksi/daftar-transaksi.component';
import { PaketComponent } from './components/paket/paket.component';
import { WatcherComponent } from './components/dashboard/watcher/watcher.component';
import { FilmmakerComponent } from './components/dashboard/filmmaker/filmmaker.component';
import { ProfilComponent } from './components/profil/profil.component';
import { KategoriComponent } from './components/kategori/kategori.component';
import { FilmDetailComponent } from './components/film-detail/film-detail.component';
import { FilmUploadComponent } from './components/film-upload/film-upload.component';

const appRoutes: Routes = [
  {path: 'welcome', component: HomeComponent, canActivate:[NotAuthGuard]},
  {path: 'login', component: LoginComponent, canActivate:[NotAuthGuard]},
  {path: 'register', component: RegisterComponent, canActivate:[NotAuthGuard]},
  {path: 'activate/:token', component: ActivateComponent, canActivate:[NotAuthGuard]},
  {path: '', component: DashboardComponent, canActivate:[AuthGuard]},
  {path: 'transaksi', component: DaftarTransaksiComponent, canActivate:[AuthGuard]},
  {path: 'paket', component: PaketComponent, canActivate:[AuthGuard]},
  {path: 'profil', component: ProfilComponent, canActivate:[WatcherGuard]},
  {path: 'kategori/:name', component: KategoriComponent, canActivate:[WatcherGuard]},
  {path: 'film/:id', component: FilmDetailComponent, canActivate:[WatcherGuard]},
  {path: 'film-upload', component: FilmUploadComponent, canActivate:[FilmmakerGuard]}
]

@NgModule({
  declarations: [
    AppComponent,
    NavbarComponent,
    HomeComponent,
    RegisterComponent,
    ActivateComponent,
    LoginComponent,
    DashboardComponent,
    DaftarTransaksiComponent,
    PaketComponent,
    WatcherComponent,
    FilmmakerComponent,
    ProfilComponent,
    FilmUploadComponent,
    KategoriComponent,
    FilmDetailComponent,
    FilterPipe,
    OrderPipe,
    TruncatePipe
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    RouterModule.forRoot(appRoutes),
    MaterializeModule,
    FileUploadModule,
    NgxPaginationModule
  ],
  providers: [
    NotAuthGuard,
    AuthGuard,
    WatcherGuard,
    FilmmakerGuard,
    AuthService,
    UserService,
    WatcherService,
    FilmmakerService,
    FilmService,
    TransaksiService,
    ValidateService,
    ProviderService
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }


