import { Component, OnInit } from '@angular/core';
import { DomSanitizer, SafeResourceUrl, SafeUrl } from '@angular/platform-browser';
import { FileUploader } from 'ng2-file-upload';

import { FilmmakerService } from './../../services/filmmaker.service';
import { ValidateService } from './../../services/validate.service';
import { ProviderService } from './../../services/provider.service';

@Component({
  selector: 'app-film-upload',
  templateUrl: './film-upload.component.html',
  styleUrls: ['./film-upload.component.css']
})
export class FilmUploadComponent implements OnInit {
  
  judul: String;
  harga: Number;
  durasi: String;
  pemain: any = [];
  description: String;

  optKategori = [
    { value:'Action', checked: false },
    { value:'Comedy', checked: false },
    { value:'Horror', checked: false },
    { value:'Romance', checked: false },
  ]

  uploaderVideo:FileUploader = new FileUploader({url:'https://mesvie.herokuapp.com/mesvie/video-upload', authToken: this.filmmakerService.loadToken()});
  previewVideo:SafeResourceUrl;
  uploaderCover:FileUploader = new FileUploader({url:'https://mesvie.herokuapp.com/mesvie/cover-upload', authToken: this.filmmakerService.loadToken()});
  previewCover:SafeUrl;

  constructor( 
    private filmmakerService: FilmmakerService,
    private validate: ValidateService,
    private provider: ProviderService,
    private sanitizer: DomSanitizer 
  ) { }

  ngOnInit() {
    this.getPreview();	
  }

  getPreview() {
    this.uploaderVideo.onAfterAddingFile = (fileItem) => {
      if(this.uploaderVideo.queue.length > 1)
        this.uploaderVideo.queue[0].remove();
      this.previewVideo  = this.sanitizer.bypassSecurityTrustResourceUrl(window.URL.createObjectURL(fileItem._file));
    }
    this.uploaderCover.onAfterAddingFile = (fileItem) => {
      if(this.uploaderCover.queue.length > 1)
        this.uploaderCover.queue[0].remove();
      this.previewCover  = this.sanitizer.bypassSecurityTrustUrl(window.URL.createObjectURL(fileItem._file));
    }
  }

  onAddFilm() {
    const film = {
      judul: this.judul,
      video: '',
      cover: '',
      harga: this.harga,
      durasi: this.durasi,
      kategori: this.getCheckedBox(),
      pemain: this.pemain,
      description: this.description
    }

    if(this.validate.filmAdd(film)) {
      this.uploaderVideo.uploadAll();
      this.uploaderVideo.onCompleteItem = (item: any, response: any, status: any, headers: any) => {
        var data = JSON.parse(response);
        film.video = data.filename;
        
        this.uploaderCover.uploadAll();
        this.uploaderCover.onCompleteItem = (item: any, response: any, status: any, headers: any) => {
          var data = JSON.parse(response);
          film.cover = data.filename;

          this.filmmakerService.filmAdd(film).subscribe(data => {
            if(data.success)
              this.provider.toast(data.message, 2000, 'Green', '/filmmaker');
            else
              this.provider.toast(data.message, 2000, 'Red');
          });
        };
      };
    }
  }

  onCheckKategori(kategori) {
    for (var i = 0; i < this.optKategori.length; i++) {
      if(this.optKategori[i].value == kategori) {
        this.optKategori[i].checked = !this.optKategori[i].checked;
      }
    }
  }

  getCheckedBox() {
    return this.optKategori.filter(opt => opt.checked).map(opt => opt.value)
  }

  pemainAdd(chip) {
    this.pemain.push(chip.tag)
  }

  pemainDelete(chip) {
    for (var i = 0; i < this.pemain.length; i++) {
      if(this.pemain[i] == chip.tag)
        this.pemain.splice(i,1);
    }
  }

}


