import { Injectable } from '@angular/core';

@Injectable()
export class ValidateService {

  constructor() { }

  register(user) {
  	if(!user.email || !this.email(user.email) || !user.username || !user.password)
  		return false;
  	else 
  		return true;
  }

  login(user) {
    if(!user.email || !this.email(user.email) || !user.password)
      return false;
    else 
      return true;
  }

  updateProfil(user) {
    if(!user.fullname || !user.phone || !user.address || !user.bio)
      return false;
    else 
      return true;
  }

  filmAdd(film) {
    if(!film.judul || !film.harga || !film.durasi || !film.kategori || !film.pemain || !film.description)
      return false;
    else 
      return true;
  }

  filmUpdate(film) {
    if(!film.judul || !film.harga || !film.description)
      return false;
    else 
      return true;
  }

  review(review) {
    if(!review.text)
      return false;
    else 
      return true;
  }

  email(email) {
  	const re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return re.test(email);
  }
}



