import { Injectable, EventEmitter } from '@angular/core';
import { Router, NavigationEnd } from '@angular/router';
import { tokenNotExpired } from 'angular2-jwt';
import { MaterializeDirective, MaterializeAction } from 'angular2-materialize';

declare var Materialize:any;

@Injectable()
export class ProviderService {
  
  isLoading: boolean = false;

  modalFilmActions = new EventEmitter<string|MaterializeAction>();
  modalProfilActions = new EventEmitter<string|MaterializeAction>();
  modelParams = [{
      dismissible: false,
      opacity: .5,
      inDuration: 300,
      outDuration: 200,
      startingTop: '30%',
      endingTop: '11%',
      complete: function() { }
  }];

  chipsActions = new EventEmitter<string|MaterializeAction>();
  chipsPlaceholder = {
    placeholder: '+Pemain'
  };
  
  constructor( 
    private router: Router 
  ) { }

  isLogin() {
    return tokenNotExpired('id_token');
  }

  getAuthType() {
    return JSON.parse(localStorage.getItem('user')).type;
  }

  navRoute(route) {
    this.router.navigate([route]);
    this.getBackTop();
  }

  openModalFilm() {
    this.modalFilmActions.emit({action:"modal",params:['open']});
  }
  closeModalFilm() {
    this.modalFilmActions.emit({action:"modal",params:['close']});
  }

  openModalProfil() {
    this.modalProfilActions.emit({action:"modal",params:['open']});
  }
  closeModalProfil() {
    this.modalProfilActions.emit({action:"modal",params:['close']});
  }

  toast(text: string, duration: number, style: string = '', route: string = this.router.url){
    Materialize.toast(text, duration, style, () => { this.navRoute(route); });
    this.getBackTop();
  }

  getBackTop() {
    this.router.events.subscribe((evt) => {
        if (!(evt instanceof NavigationEnd)) {
            return;
        }
        window.scrollTo(0, 0)
    });
  }

}


