import { Injectable } from '@angular/core';
import { Http, Headers } from '@angular/http';
import { Observable } from 'rxjs';
import 'rxjs/add/operator/map';

@Injectable()
export class WatcherService {
  
  constructor( private http: Http ) { }

  loadToken() {
    return localStorage.getItem('id_token');
  }

  tonton(filmId) {
    let headers = new Headers();
    headers.append('Authorization', this.loadToken());
    return this.http.get('mesvie/tonton/'+filmId, {headers: headers}).map(res => res.json());
  }

  watchlater(action) {
    let headers = new Headers();
    headers.append('Authorization', this.loadToken());
    headers.append('Content-Type','application/json');
    return this.http.post('mesvie/watchlater', action, {headers: headers}).map(res => res.json());
  }

  watchlaterCek(filmId) {
    let headers = new Headers();
    headers.append('Authorization', this.loadToken());
    return this.http.get('mesvie/watchlater-cek/'+filmId, {headers: headers}).map(res => res.json());
  }

  love(action) {
    let headers = new Headers();
    headers.append('Authorization', this.loadToken());
    headers.append('Content-Type','application/json');
    return this.http.post('mesvie/love', action, {headers: headers}).map(res => res.json());
  }

  loveCek(filmId) {
    let headers = new Headers();
    headers.append('Authorization', this.loadToken());
    return this.http.get('mesvie/love-cek/'+filmId, {headers: headers}).map(res => res.json());
  }

  review(review) {
    let headers = new Headers();
    headers.append('Authorization', this.loadToken());
    headers.append('Content-Type','application/json');
    return this.http.post('mesvie/review', review, {headers: headers}).map(res => res.json());
  }

}


