import { Injectable } from '@angular/core';
import { Http, Headers } from '@angular/http';

@Injectable()
export class AuthService {
  authToken: any;

  constructor(private http: Http) { }

  register(user) {
  	let headers = new Headers();
  	headers.append('Content-Type','application/json');
  	return this.http.post('mesvie/register', user, {headers: headers}).map(res => res.json());
  }

  activate(token) {
    let headers = new Headers();
    return this.http.put('mesvie/activate/'+token, {headers: headers}).map(res => res.json());
  }

  login(user) {
  	let headers = new Headers();
  	headers.append('Content-Type','application/json');
  	return this.http.post('mesvie/login', user, {headers: headers}).map(res => res.json());
  }

  loadToken() {
    return localStorage.getItem('id_token');
  }
  
  storeData(token, user) {
    localStorage.setItem('id_token', token);
    localStorage.setItem('user', JSON.stringify(user));
    this.authToken = token;
  }

  logout() {
    this.authToken = null;
    localStorage.clear();
  }
}


