import { Component, OnInit } from '@angular/core';

import { TransaksiService } from './../../services/transaksi.service';
import { ProviderService } from './../../services/provider.service';

@Component({
  selector: 'app-daftar-transaksi',
  templateUrl: './daftar-transaksi.component.html',
  styleUrls: ['./daftar-transaksi.component.css']
})
export class DaftarTransaksiComponent implements OnInit {
  
  transaksis: any;

  constructor(
  	private transaksiService: TransaksiService,
  	private provider: ProviderService
  ) { }

  ngOnInit() {
    this.getTransaksi();
  }

  getTransaksi() {
    this.provider.isLoading = true;

  	this.transaksiService.transaksiGet().subscribe(data => {
  		this.transaksis = data.data;
      this.provider.isLoading = false;
  	});
  }

}



