import { Injectable } from '@angular/core';
import { Http, Headers } from '@angular/http';
import { Observable } from 'rxjs';
import 'rxjs/add/operator/map';

@Injectable()
export class FilmmakerService {   
  
  constructor( private http: Http ) { }

  loadToken() {
    return localStorage.getItem('id_token');
  }

  filmAdd(film) {
    let headers = new Headers();
    headers.append('Authorization', this.loadToken());
    headers.append('Content-Type','application/json');
    return this.http.post('mesvie/film-add', film, {headers: headers}).map(res => res.json());
  }

  filmUpdate(film) {
    let headers = new Headers();
    headers.append('Authorization', this.loadToken());
    headers.append('Content-Type','application/json');
    return this.http.post('mesvie/film-update', film, {headers: headers}).map(res => res.json());
  }

}


