import { Component, OnInit, OnDestroy } from '@angular/core';
import { ActivatedRoute, Params } from '@angular/router';

import { FilmService } from './../../services/film.service';
import { ProviderService } from './../../services/provider.service';

@Component({
  selector: 'app-kategori',
  templateUrl: './kategori.component.html',
  styleUrls: ['./kategori.component.css']
})
export class KategoriComponent implements OnInit, OnDestroy {

  kategori: string;
  films: any;
  
  loadmoreKategoriId: any;
  loadmoreKategori: any;
  kategoriPerPage: number = 4;
  
  constructor( 
    private filmService: FilmService,
    private provider: ProviderService, 
    private route: ActivatedRoute
  ) { }

  ngOnInit() {
  	this.getKategori();
  	this.getFilmAll();
  }

  getKategori() {
    this.route.params.subscribe(params => {
        this.kategori = params['name'];
     });
  }

  getFilmAll() {
    this.provider.isLoading = true;

  	this.filmService.filmAll().subscribe(data => {
  	  this.films = data.data;
      this.initLoadmore();
      this.provider.isLoading = false;
  	});
  }

  onFilmDetail(filmId) {
  	this.provider.navRoute('/film/'+filmId);
  }

  initLoadmore() {
    if(this.films.length <= this.kategoriPerPage)
      this.loadmoreKategori = null;
    else
      this.loadmoreKategori = { hide: true, disable: false }
  }

  onLoadmoreKategori() {
    this.loadmoreKategori.hide = false;
    this.loadmoreKategori.disable = true;

    this.loadmoreKategoriId = setInterval(() => {
      this.kategoriPerPage += 4;
      
      if(this.films.length <= this.kategoriPerPage)
        this.loadmoreKategori = null;
      else {
        this.loadmoreKategori.hide = true;
        this.loadmoreKategori.disable = false;
      }
      clearInterval(this.loadmoreKategoriId);
    }, 2000);
  }

  ngOnDestroy() {
    if(this.loadmoreKategoriId)
      clearInterval(this.loadmoreKategoriId);
  }

}


