import { Injectable } from '@angular/core';
import { Http, Headers } from '@angular/http';
import { Observable } from 'rxjs';
import 'rxjs/add/operator/map';

@Injectable()
export class TransaksiService {   
  
  constructor( private http: Http ) { }

  loadToken() {
    return localStorage.getItem('id_token');
  }

  purchase(payment) {
    let headers = new Headers();
    headers.append('Authorization', this.loadToken());
    headers.append('Content-Type','application/json');
    return this.http.post('mesvie/purchase', payment, {headers: headers}).map(res => res.json());
  }

  transaksiAdd(transaksi) {
    let headers = new Headers();
    headers.append('Authorization', this.loadToken());
    headers.append('Content-Type','application/json');
    return this.http.post('mesvie/transaksi-add', transaksi, {headers: headers}).map(res => res.json());
  }

  transaksiGet() {
    let headers = new Headers();
    headers.append('Authorization', this.loadToken());
    return this.http.get('mesvie/transaksi-all', {headers: headers}).map(res => res.json());
  }

}


